@import url("https://fonts.googleapis.com/css2?family=Jersey+10&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mukta+Mahee&display=swap");

.home {
  width: 100%;
  height: 90vh;
  display: flex;
  align-items: center;
}

.home-row {
  padding-left: 28px;
  padding-right: 15px;
  position: relative;
  top: -50px;
  flex-wrap: wrap-reverse;
}

.txt-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.display-4 {
  font-family: "Jersey 10", sans-serif;
  font-weight: 400;
  font-style: normal;
  line-height: 1.1;
}

.txt {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 1.125rem;
  width: 80%;
}

.joinBtn {
  font-family: "Mukta Mahee", sans-serif;
  font-size: 1.0625rem;
  font-weight: 500;
  color: white;
  background-color: rgba(164, 106, 6, 0.9);
  padding: 5px 24px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.joinBtn:hover {
  background-color: rgba(164, 106, 6, 1);
}

/* #----| Responsive Design |----# */

@media only screen and (max-width: 768px) {
  .home-row {
    position: relative;
    top: -55px;
  }

  .txt-container {
    margin-top: 35px;
    text-align: center;
  }

  .img-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .img-fluid {
    width: 70%;
  }

  .txt {
    width: 100%;
  }
}

@media only screen and (max-width: 450px) {
  .txt {
    font-size: 1rem;
  }
}
